.wp-block-ecb-blocks-photogrid {
	display: grid;
	grid-template-columns: 30% 70%;
	min-height: 440px;
	animation: ecbFadeIn 0.3s ease;

	.grid {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 0.5rem;
		margin-right: 0.5rem;

		div {
			display: grid;
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 1fr;
			gap: 0.5rem;
			position: relative;
			overflow: hidden;
			transition: all 1s ease-in-out;
		}

		div>a {
			position: absolute;
			object-fit: cover;
			width: 100%;
			height: 100%;
			min-width: 100%;
			min-height: 100%;
		}

		div>a img {
			position: absolute;
			object-fit: cover;
			transform-origin: center;
			transform: scale(1);
			width: 100%;
			height: 100%;
			min-width: 100%;
			min-height: 100%;
			transition: all 0.8s ease;
		}

		div:hover>a img {
			transform: scale(1.2);
		}
	}

	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 2.7rem 3rem 3.5rem 3rem;
		background: var(--tan);

		h1,
		h2,
		p,
		a {
			align-self: flex-start;
		}

		p {
			margin: 1rem 0px 1.5rem 0px;
		}
	}

	@media only screen and (max-width: 1200px) {
		grid-template-columns: 40% 60%;
	}

	@media only screen and (max-width: 740px) {
		grid-template-columns: 1fr;

		.grid {
			margin-bottom: 0.5rem;
			margin-right: 0rem;
			min-height: 400px;
		}
	}

	@media only screen and (max-width: 640px) {
		.content {
			padding: 0.5rem 2rem 2.5rem 2rem;

			p {
				margin: 1rem 0px 1rem 0px;
			}
		}
	}
}